@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono);
body {
  font-family: 'Ubuntu Mono', monospace !important;
}

.welcome {
  position: relative;
  height: 100vh;
  min-height: 700px;
  background: url(/oregand.gitgub.io/static/media/map.be76db28.jpg) no-repeat center center fixed;
  background-size: cover;
}

.welcome:before {
  position: absolute;
  z-index: 0;
  content: '';
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.8;
  background: #007bff;
}

@media (max-width: 767.98px) {
  .welcome .inner-wrapper {
    text-align: center;
  }
}

@media (max-width: 400px) {
  .welcome .welcome-heading {
    font-size: 2.415rem;
  }
}

.section-title {
  position: relative;
}

.section-title:after {
  content: '';
  width: 30px;
  height: 2px;
  background: #007bff;
  position: absolute;
  left: 50%;
  margin-left: -15px;
  bottom: -20px;
}

.feature .icon {
  border-radius: 50%;
  min-width: 65px;
  height: 65px;
  line-height: 65px;
  text-align: center;
  box-shadow: 0 2px 4px rgba(33, 37, 41, 0.1), 0 7px 14px rgba(33, 37, 41, 0.1);
  font-size: 27px;
}

.section {
  border-bottom: 1px solid #eaebed;
}

.section-invert {
  background: #f9fafc;
}

.testimonials .avatar {
  max-width: 80px;
  overflow: hidden;
}

